"use client"

import { useState } from "react"
import { useRouter } from "next/navigation"
import { RetroGrid } from "@/components/ui/retro-grid"
import { LineShadowText } from "@/components/ui/line-shadow-text"
import { SmoothCursor } from "@/components/ui/smooth-cursor"
import { Input } from "@/components/ui/input"
import { RainbowButton } from "@/components/ui/rainbow-button"

const comparisonPoints = [
  {
    title: "AI-Powered Insights",
    description: "Get instant predictions for churn risk, sentiment analysis, ticket category, and priority - all in one place.",
    competitor: "Manual analysis required",
  },
  {
    title: "Real-time Analysis",
    description: "Process support tickets in seconds with our advanced ML models trained on thousands of support interactions.",
    competitor: "Delayed reporting",
  },
  {
    title: "Actionable Recommendations",
    description: "Receive specific recommendations to improve customer satisfaction and reduce churn rates.",
    competitor: "Basic metrics only",
  },
  {
    title: "Seamless Integration",
    description: "Works with your existing support systems. Built for modern SaaS platforms.",
    competitor: "Manual data export",
  },
]

export default function Home() {
  const [ticketText, setTicketText] = useState("")
  const router = useRouter()

  const handleAnalyze = () => {
    if (ticketText.trim()) {
      // Encode ticket text and redirect to results page
      router.push(`/result?ticket=${encodeURIComponent(ticketText)}`)
    }
  }

  return (
    <div className="w-full overflow-hidden bg-white dark:bg-black">
      <SmoothCursor />

      {/* Section 1: Hero */}
      <section className="relative w-full min-h-screen flex items-center justify-center overflow-hidden">
        <RetroGrid angle={65} cellSize={60} opacity={0.3} />
        <div className="relative z-10 flex flex-col items-center justify-center gap-8 px-4 text-center">
          <h1 className="text-6xl md:text-7xl font-bold">
            <LineShadowText
              shadowColor="rgb(59, 130, 246)"
              className="text-black dark:text-white"
            >
              Sentra AI
            </LineShadowText>
          </h1>
          <p className="max-w-2xl text-lg md:text-xl text-gray-600 dark:text-gray-300">
            Intelligent customer support analysis powered by advanced AI. Predict churn, analyze sentiment, and prioritize tickets automatically.
          </p>
        </div>
      </section>

      {/* Section 2: Ticket Input */}
      <section className="w-full py-20 px-4 bg-gray-50 dark:bg-gray-950">
        <div className="max-w-3xl mx-auto">
          <h2 className="text-4xl font-bold text-center mb-4 text-black dark:text-white">
            Analyze Your Ticket
          </h2>
          <p className="text-center text-gray-600 dark:text-gray-400 mb-8">
            Paste your customer support ticket below and let our AI provide insights.
          </p>

          <div className="bg-white dark:bg-gray-900 rounded-lg shadow-lg p-8">
            <div className="space-y-6">
              <div>
                <label htmlFor="ticket" className="block text-sm font-medium mb-2 text-black dark:text-white">
                  Support Ticket
                </label>
                <textarea
                  id="ticket"
                  value={ticketText}
                  onChange={(e) => setTicketText(e.target.value)}
                  placeholder="Paste your customer support ticket here..."
                  className="w-full h-32 p-4 border border-gray-300 dark:border-gray-700 rounded-lg bg-white dark:bg-gray-800 text-black dark:text-white placeholder-gray-400 dark:placeholder-gray-500 focus:outline-none focus:ring-2 focus:ring-blue-500 resize-none"
                />
              </div>

              <div className="flex justify-center">
                <RainbowButton
                  onClick={handleAnalyze}
                  disabled={!ticketText.trim()}
                  size="lg"
                  className="px-8"
                >
                  Analyze Ticket
                </RainbowButton>
              </div>
            </div>
          </div>
        </div>
      </section>

      {/* Section 3: How We Are Different */}
      <section className="w-full py-20 px-4 bg-white dark:bg-black">
        <div className="max-w-6xl mx-auto">
          <h2 className="text-4xl font-bold text-center mb-4 text-black dark:text-white">
            How We Are Different
          </h2>
          <p className="text-center text-gray-600 dark:text-gray-400 mb-12">
            Sentra AI stands out from traditional support tools with intelligent automation.
          </p>

          <div className="grid md:grid-cols-2 gap-8">
            {comparisonPoints.map((point, index) => (
              <div
                key={index}
                className="p-6 rounded-lg border border-gray-200 dark:border-gray-800 bg-gray-50 dark:bg-gray-900 hover:shadow-lg transition-shadow"
              >
                <h3 className="text-xl font-semibold mb-3 text-black dark:text-white">
                  {point.title}
                </h3>
                <p className="text-gray-700 dark:text-gray-300 mb-4">
                  {point.description}
                </p>
                <div className="pt-4 border-t border-gray-200 dark:border-gray-800">
                  <p className="text-sm text-gray-500 dark:text-gray-500">
                    <span className="line-through">Traditional: {point.competitor}</span>
                  </p>
                </div>
              </div>
            ))}
          </div>
        </div>
      </section>

      {/* Footer */}
      <section className="w-full py-12 px-4 bg-gray-50 dark:bg-gray-950 border-t border-gray-200 dark:border-gray-800">
        <div className="max-w-6xl mx-auto text-center">
          <p className="text-gray-600 dark:text-gray-400">
            Â© 2026 Sentra AI. All rights reserved.
          </p>
        </div>
      </section>
    </div>
  )
}
